body {
    margin: 0;
    font-family: 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 60px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
    display: block;
    margin: auto;
}

.loading {
    text-align: center;
    display: block;
    margin: auto;
    position: relative;
    height: 100px;
    width: auto;
}

.caja-color {
    display: inline-block;
    width: 100px;
    height: 50px;
    margin: 5px;
    border: 1px solid black;
    text-align: center;
    padding: 4px;
    cursor: pointer;
}

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-items: center;
    flex-wrap: wrap;
}